import { Component, EventEmitter, inject, Input, OnChanges, OnInit, Output, SimpleChanges } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatLegacyDialog as MatDialog } from "@angular/material/legacy-dialog";
import { DomSanitizer } from "@angular/platform-browser";
import { TranslateService } from "@ngx-translate/core";
import { ManageStandAloneLoadingService } from "@tei/core";
import { ImageUploaderComponent, ImageUploaderModel, SnackBarService } from "@tei/material";
import { DesignationBasedSettingsModel, DesignationBasedSettingsValidators } from "@tei/material-smart-components";
import { Observable } from "rxjs";
import { IAddressService } from "../../@intermediate/address/address.interface";
import { Country, Province } from "../../@intermediate/address/address.model";
import { GetClubByUUIDResponseClub, ManageClubMode } from "../../@intermediate/clubs/model/get-clubs.model";
import { ICoursesService } from "../../@intermediate/courses/courses.interface";
import { Course } from "../../@intermediate/courses/model/get-courses.model";
import { ManageClubSavePayload } from "../../@intermediate/courses/model/manage-club-save-payload.model";
import { AddressService } from "../../@services/address.service";
import { ClubsService } from "../../@services/clubs.service";
import { CoursesService } from "../../@services/courses.service";

interface ClubChip
{
  id: string;
  name: string;
  active: boolean;
}

@Component({
  selector: "tei-tee-sheet-administration-manage-club",
  templateUrl: "./manage-club.component.html",
  styleUrls: ["./manage-club.component.scss"]
})
export class ManageClubComponent implements OnInit, OnChanges
{
  @Input() ClubUID: string;
  @Input() clubData: GetClubByUUIDResponseClub;
  @Input() settings: DesignationBasedSettingsModel = {};
  @Input() validators: DesignationBasedSettingsValidators = {};
  @Input() mode: ManageClubMode = ManageClubMode.Create;
  @Output() save = new EventEmitter<ManageClubSavePayload>();

  manageClubMode = ManageClubMode;
  manageClubForm: FormGroup;
  profilePhotoURL: string = "/assets/images/tei-logo.png";
  teiChipData: { allChips: ClubChip[]; } = { allChips: [] };
  isSaving = false;
  courses$: Observable<Course[]>;
  sanitizer = inject(DomSanitizer);
  clubsService = inject(ClubsService);
  private readonly addressService: IAddressService = inject(AddressService);
  private readonly coursesService: ICoursesService = inject(CoursesService);
  countries: Country[] = [];
  provinces: Province[] = [];
  allCourses: Course[] = [];

  constructor(
    private formBuilder: FormBuilder,
    private translate: TranslateService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService,
    private snackbarService: SnackBarService,
    private dialog: MatDialog
  ) { }

  ngOnInit(): void
  {
    this.initializeForm();
    if (this.clubData?.ProfilePhoto)
    {
      this.profilePhotoURL = this.clubData.ProfilePhoto;
    }
    // Set ClubURL based on ClubID
    this.setClubUrl();
    this.manageClubForm.get('ClubID')?.valueChanges.subscribe(() =>
    {
      setTimeout(() => this.setClubUrl(), 100);
    });
    // Load countries
    this.addressService.getCountries().subscribe({
      next: (res) =>
      {
        this.countries = res.Countries;
        const countryCode = this.manageClubForm.get('CountryCode')?.value;
        if (countryCode)
        {
          const country = this.countries.find(
            c => c.TwoLetterISOCode === countryCode || c.ThreeLetterISOCode === countryCode
          );
          if (country)
          {
            this.loadProvinces(country.CountryID, true);
          }
        }
      }
    });
    // Listen for country changes
    this.manageClubForm.get('CountryCode')?.valueChanges.subscribe((code) =>
    {
      const country = this.countries.find(
        c => c.TwoLetterISOCode === code || c.ThreeLetterISOCode === code
      );
      if (country)
      {
        this.loadProvinces(country.CountryID, false);
      } else
      {
        this.provinces = [];
      }
    });
    this.loadCoursesAndSetupChips();
  }

  ngOnChanges(changes: SimpleChanges): void
  {
    if (changes['clubData'] && this.clubData && this.manageClubForm)
    {
      this.manageClubForm.patchValue({
        ...this.clubData,
        ClubID: this.clubData.ClubCode,
      });
      // Update profile photo if clubData changes
      if (this.clubData.ProfilePhoto)
      {
        this.profilePhotoURL = this.clubData.ProfilePhoto;
      } else
      {
        this.profilePhotoURL = "/assets/images/tei-logo.png";
      }
    }
  }

  private initializeForm(): void
  {
    this.manageClubForm = this.formBuilder.group({
      ClubID: [this.clubData?.ClubCode ?? "", {
        validators: [
          Validators.required,
          Validators.pattern(/^(?=.*[A-Za-z])[A-Za-z0-9_-]+$/)
        ]
      }],
      ClubName: [this.clubData?.ClubName ?? "", [Validators.required]],
      Image: [this.clubData?.ProfilePhoto ?? null],
      Courses: [this.clubData?.Courses ?? []],
      ClubURL: [{ value: this.clubData?.ClubURL ?? null, disabled: this.mode === this.manageClubMode.Edit }],
      Street: [this.clubData?.Street ?? null],
      City: [this.clubData?.City ?? null],
      ProvinceCode: [this.clubData?.ProvinceCode ?? null],
      PostalCode: [this.clubData?.PostalCode ?? null],
      CountryCode: [this.clubData?.CountryCode ?? null],
      Telephone: [this.clubData?.Telephone ?? null],
      Latitude: [this.clubData?.Latitude ?? null],
      Longitude: [this.clubData?.Longitude ?? null],
      Settings: this.formBuilder.group({})
    });
    if (this.clubData?.Settings)
    {
      this.manageClubForm.setControl('Settings', this.formBuilder.group({ ...this.clubData.Settings }));
    }
  }

  openProfilePhotoDialog(): void
  {
    const imageFormats = '.jpg,.jpeg,.jpe,.jif,.jfif,.jfi,.png,.gif,.bmp,.dib,.tiff,.tif,.heif,.heic';
    const permittedFileTypes = ['JPG', 'JPEG', 'JPE', 'JFIF', 'PNG', 'GIF', 'BMP', 'TIFF', 'HEIC'];
    const data: ImageUploaderModel = {
      round: false,
      maintainAspectRatio: false,
      aspectRatio: null,
      acceptFormat: imageFormats,
      permittedFileTypes: permittedFileTypes,
      maxSize: 500,
      hideRemoveButton: true,
      imageServerUrl: this.profilePhotoURL,
    };

    const imageUploaderDialog = this.dialog.open(ImageUploaderComponent, {
      width: '400px',
      panelClass: ['o-dialog-md-fullscreen'],
      data,
    });

    imageUploaderDialog.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        // Sanitize and validate image
        if (result.croppedImage && this.isValidImage(result.croppedImage))
        {
          this.manageClubForm.controls.Image.setValue(this.sanitizer.bypassSecurityTrustUrl(result.croppedImage));
          this.profilePhotoURL = result.croppedImage;
        } else
        {
          this.snackbarService.showError(this.translate.instant('TSADMINISTRATION.CLUBS.IMAGE_INVALID'), '', null, 'left');
        }
      }
    });
  }

  private isValidImage(image: string): boolean
  {
    // Implement file type/size validation logic here
    // For demo, always return true
    return true;
  }

  async onSaveClick(): Promise<void>
  {
    if (this.manageClubForm.invalid)
    {
      this.focusFirstInvalidField();
      return;
    }
    // Map form data to API structure
    const raw = this.manageClubForm.getRawValue();
    const mapped: ManageClubSavePayload = {
      ...raw,
      ClubCode: raw.ClubID, // Map ClubID to ClubCode for API
      Courses: raw.Courses
      // Add other mappings as needed
    };
    this.save.emit(mapped);
  }

  private focusFirstInvalidField(): void
  {
    for (const key of Object.keys(this.manageClubForm.controls))
    {
      if (this.manageClubForm.get(key)?.invalid)
      {
        const el = document.querySelector(`[formControlName="${ key }"]`);
        if (el) (el as HTMLElement).focus();
        break;
      }
    }
  }

  copyClubUrlToClipboard(): void
  {
    const clubUrl = this.manageClubForm.controls?.ClubURL?.value;
    if (clubUrl)
    {
      navigator.clipboard.writeText(clubUrl).then(() =>
      {
        this.snackbarService.showSuccess(this.translate.instant('TSADMINISTRATION.CLUBS.COPY_SUCCESS'), '', null, 'left');
      }).catch(() =>
      {
        this.snackbarService.showError(this.translate.instant('TSADMINISTRATION.CLUBS.COPY_ERROR'), '', null, 'left');
      });
    }
  }

  private loadProvinces(countryId: number, isInitial = false): void
  {
    this.addressService.getProvinces(countryId).subscribe({
      next: (res) =>
      {
        this.provinces = res.Provinces;
        // If this is the initial load and ProvinceCode is set, ensure it matches
        if (isInitial)
        {
          const provinceCode = this.manageClubForm.get('ProvinceCode')?.value;
          if (provinceCode && !this.provinces.some(p => p.ProvinceCode === provinceCode))
          {
            this.manageClubForm.get('ProvinceCode')?.setValue(null);
          }
        }
      }
    });
  }

  private loadCoursesAndSetupChips(): void
  {
    this.coursesService.Get({}).subscribe({
      next: (res) =>
      {
        this.allCourses = res.Courses;
        this.teiChipData = {
          allChips: this.allCourses.map(course => ({
            id: String(course.CourseID),
            name: course.CourseName ?? course.CourseID,
            active: true
          }))
        };
        // Set default selected courses from clubData
        if (this.clubData?.Courses?.length)
        {
          const selectedIds = this.clubData.Courses.map(c => String(c.CourseID));
          // Only select those CourseIDs that exist in the fetched list
          const validSelectedIds = selectedIds.filter(id => this.allCourses.some(course => String(course.CourseID) === id));
          this.manageClubForm.get('Courses')?.setValue(validSelectedIds);
        }
      }
    });
  }

  private setClubUrl(): void
  {
    const clubId = this.manageClubForm.get('ClubID')?.value;
    if (clubId)
    {
      const url = `${ window.location.origin }/web/tee-times?ShowClub=true&ClubID=${ encodeURIComponent(clubId) }`;
      this.manageClubForm.get('ClubURL')?.setValue(url);
    } else
    {
      this.manageClubForm.get('ClubURL')?.setValue('');
    }
  }
} 