import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { CountriesResponse, ProvincesResponse } from "../@intermediate/address/address.model";

@Injectable({
  providedIn: "root"
})
export class AddressService
{
  private readonly baseUrl: string;

  constructor(
    @Inject("IAPIBaseUrlProvider") private apiBaseUrlProvider: IAPIBaseUrlProvider,
    private http: HttpClient
  )
  {
    this.baseUrl = this.apiBaseUrlProvider.getBaseUrl();
  }

  getCountries(): Observable<CountriesResponse>
  {
    const url = `${ this.baseUrl }/Location/Countries`;
    return this.http.get<CountriesResponse>(url);
  }

  getProvinces(countryId: number): Observable<ProvincesResponse>
  {
    const url = `${ this.baseUrl }/Location/Provinces`;
    return this.http.get<ProvincesResponse>(url, { params: { CountryID: countryId } });
  }
} 