import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { IClubsService } from "../@intermediate/clubs/clubs.interface";
import { DeleteClubRequest, DeleteClubResponse, GetClubByUUIDResponse, GetClubsListRequest, GetClubsRequest, GetClubsResponse, PostClubOrderRequest, PostClubOrderResponse } from "../@intermediate/clubs/model/get-clubs.model";
import { CreateClubRequest, CreateClubResponse } from "../@intermediate/clubs/model/post-clubs.model";

@Injectable({
  providedIn: "root"
})
export class ClubsService implements IClubsService
{
  private readonly serviceUrl: string;
  constructor(@Inject("IAPIBaseUrlProvider") protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + "/TShAdministration/Club";
  }

  Get(request: GetClubsRequest): Observable<GetClubsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetClubsResponse>(apiUrl, { params: request as any });
  }

  GetList(request: GetClubsListRequest): Observable<GetClubsResponse>
  {
    const apiUrl = this.serviceUrl + "/GetClub";
    return this.http.get<GetClubsResponse>(this.serviceUrl, { params: request as any });
  }

  Delete(request: DeleteClubRequest): Observable<DeleteClubResponse>
  {
    const apiUrl = this.serviceUrl + "/Delete";
    return this.http.delete<DeleteClubResponse>(apiUrl, { params: { ClubUID: request.clubUID } as any });
  }

  GetClubByUUID(uuid: string): Observable<GetClubByUUIDResponse>
  {
    const apiUrl = this.serviceUrl + "/GetClub";
    return this.http.get<any>(apiUrl, { params: { ClubUID: uuid } as any });
  }

  SortItem(request: PostClubOrderRequest): Observable<PostClubOrderResponse>
  {
    const apiUrl = this.serviceUrl + "/Reorder";
    return this.http.post<PostClubOrderResponse>(apiUrl, request);
  }

  PostClub(request: CreateClubRequest): Observable<CreateClubResponse>
  {
    const apiUrl = this.serviceUrl + "/PostClub";
    return this.http.post<CreateClubResponse>(apiUrl, request);
  }
} 